<template>
<div>
  <div id="map"></div>
  <template id="controls">
    <div id="select-draw-type">
      <el-select v-model="drawType">
        <el-option v-for="i in drawTypeArr" :key="i"
        :value="i" :label="i"></el-option>
      </el-select>
    </div>
  </template>
</div>
</template>

<script>
import $ from 'jquery';
import {Map, View} from 'ol';
import TileLayer from 'ol/layer/Tile';
import OSM from 'ol/source/OSM';

import {Control} from 'ol/control';
import {createStringXY, toStringXY} from 'ol/coordinate';

import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';

import CircleStyle from 'ol/style/Circle';
import {Style, Fill, Stroke} from 'ol/style';

import {createRegularPolygon} from 'ol/interaction/Draw';
import Polygon from 'ol/geom/Polygon';
import Draw from 'ol/interaction/Draw';

export default {
  name: 'map-drawer',
  watch: {
    drawType: function (val, oldVal) {
      if (val !== oldVal) {
        this.map.removeInteraction(this.draw);
        this.addDrawInteraction(val);
      }
    }
  },
  data() {
    return {
      map: {},
      view: {},
      layers: [],
      drawLayer: {},   // 绘图层
      drawSource: {},  // 绘图源
      draw: {},        // 绘图对象
      drawTypeArr: [
        'None',
        'Point',
        'LineString',
        'Polygon',
        'Circle',
        'Square',
        'Box',
      ],
      drawType: 'Box',

      clickCount: 0,
      boxCoordinates: new Array(2),
    }
  }, // data
  methods: {
    addDrawInteraction(value) {
      if (value !== '' && value !== 'None') {
        if (this.drawSource == null) {
          this.drawSource = new VectorSource({ wrapX: false });
          this.drawLayer.setSource(this.drawSource);
        }
        let geometryFunction, maxPoints;

        if (value === 'Square') {         // 正方形
          value = 'Circle';               // 设置绘图类型为Circle
          // 设置几何信息变更函数，创建正多边形
          geometryFunction = createRegularPolygon(4);
        } else if (value === 'Box') {     // 长方形
          value = 'LineString';           // 设置绘图类型为LineString
          maxPoints = 2;                  // 设置最大点数为2
          // 设置几何信息变更函数，即设置长方形的坐标点
          geometryFunction = (coordinates, geometry) => {
            if (!geometry) {
              geometry = new Polygon(coordinates); // 多边形
            }
            let start = coordinates[0];
            let end = coordinates[1];
            geometry.setCoordinates([
              [start, [start[0], end[1]], end, [end[0], start[1]], start]
            ]);

            return geometry;
          };
        }
        
        this.draw = new Draw({
          source: this.drawSource,
          type: value,
          geometryFunction: geometryFunction,
          maxPoints: maxPoints
        });
        this.map.addInteraction(this.draw);
      } else {
        this.drawSource = null; // 清空绘制图形
        this.drawLayer.setSource(this.drawSource);
      }
    },
    preDefineDrawer() {
      this.drawSource = new VectorSource({ wrapX: false });
      this.drawLayer = new VectorLayer({
        source: this.drawSource,
        style: new Style({
          fill: new Fill({                  // 填充样式
            color: [255, 255, 255, 0.2]
          }),
          stroke: new Stroke({              // 边界样式
            color: '#ffcc33',
            width: 2
          }),
          image: new CircleStyle({          // 点要素样式
            radius: 7,
            fill: new Fill({
              color: '#ffcc33'
            })
          })
        })
      });
    },
    preDefine() {
      // view
      this.view = new View({
        center: [114, 30],
        zoom: 3,
        projection: 'EPSG:4326'
      });
      // layers
      this.layers = [
        new TileLayer({
          source: new OSM()
        }),
        this.drawLayer
      ]
    },
    createMap() {
      const self = this;
      self.map = new Map({
        target: 'map',
        view: this.view,
        layers: this.layers
      });
      // 监听鼠标点击事件
      self.map.on('click', e => {
        let coordinate = toStringXY(e.coordinate, 6);
        let zoom = e.map.getView().getZoom();
        self.$notify({
          message: coordinate + ', ' + zoom,
          type: 'success'
        });

        // 捕获每次点击的坐标信息
        self.clickCount++;
        if (self.clickCount % 2 === 1) { // 获取第二次点击的坐标
          self.boxCoordinates[0] = self.map.getPixelFromCoordinate(e.coordinate);
        } else {                         // 获取第二次点击的坐标
          self.boxCoordinates[1] = self.map.getPixelFromCoordinate(e.coordinate);
        }
      });
      // 添加自定义控件
      self.map.addControl(new Control({
        element: $('#select-draw-type')[0]
      }));
      // 添加绘图标志
      self.addDrawInteraction(this.drawType);

      // 监听绘图动作
      this.draw.on('drawend', event => {
        // 清空绘制图形（方便截图）
        self.drawSource = null;
        self.drawLayer.setSource(self.drawSource);
        self.clickCount = 0;

        self.$confirm('是否下载图片？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let bc = self.boxCoordinates;
          // 调整坐标，保证保存的坐标为[左上角]，[右下角]
          if (bc[0][0] > bc[1][0]) {
            let temp = bc[0][0];
            bc[0][0] = bc[1][0];
            bc[1][0] = temp;
          }
          if (bc[0][1] > bc[1][1]) {
            let temp = bc[0][1];
            bc[0][1] = bc[1][1];
            bc[1][1] = temp;
          }

          // 打印输出框定区域
          let canvas = self.map.renderer_.canvas_;
          let ctx = canvas.getContext('2d');
          let x = bc[0][0], y = bc[0][1], width = (bc[1][0] - bc[0][0]), height = (bc[1][1] - bc[0][1])
          let rectDataWrap = ctx.getImageData(x, y, width, height);

          // 创建唯一标识符（当前时间），用于给图片命名
          let currentTime = (new Date()).getTime();

          // 创建 canvas 元素并填充图片数据
          let outputCanvas = $('<canvas style="display:none;" id="' + currentTime + '" width="' + width + '" height="' + height + '"></canvas>')[0];
          outputCanvas.getContext("2d").putImageData(rectDataWrap, 0, 0);

          // 创建下载工具
          let $a = $("<a></a>").attr("href", outputCanvas.toDataURL('image/png')).attr("download", currentTime + ".png");
          self.$message({
            type: 'success',
            message: '开始下载: [' + currentTime + '.png]'
          });
          $a[0].click();

          // 删除元素
          $('#' + currentTime).remove();
        }).catch((err) => {
          this.$message({
            type: 'info',
            message: '已取消下载 [' + err + ']'
          });          
        }); // confirm
      }); // this.draw.on('drawend')
    }
  }, // methods
  mounted() {
    // 获取屏高方法
    let h = $('.content-box').css('height');
    h = (h.split('px')[0]).toString() + 'px';
    $('#map').css('height', h);
    
    this.preDefineDrawer();  // 预定义绘图
    this.preDefine();
    this.createMap();
  }
}
</script>

<style scoped>
#map {
  height: 100%;
  width: 100%;
}
#select-draw-type {
  position: absolute;
  top: 0.5em;
  left: 3.0em;
}
</style>